module.exports = (wrap) => {

    const sliderElem = wrap.querySelectorAll('.slider_edit_img');
    const wrapListElem = wrap.querySelector('.slider_edit_images');
    const addNewElem = wrap.querySelector('.slider_edd_img input');
    const arrElem = [];


    function setEventElem(elem) {
        elem.addEventListener('click', () => {
            for (let i = 0; i < arrElem.length; i++) {
                arrElem[i].classList.remove('active');
            }
            elem.classList.add('active');
        });
    }

    for (let i = 0; i < sliderElem.length; i++) {
        arrElem.push(sliderElem[i]);
        setEventElem(sliderElem[i]);
    }


    addNewElem.addEventListener('input', function () {
        let reader = new FileReader();
        reader.addEventListener('load', function (event) {
            let newElem = document.createElement('div');
            newElem.className = 'slider_edit_img';
            newElem.innerHTML = `
                        <img src="${event.target.result}" alt="">
                        <div class="slider_img_control">
                            <button class="btn_dote btn_editing_img"><i>@</i></button>
                            <button class="btn_dote btn_delete_img"><i>#</i></button>
                        </div>`;
            arrElem.push(newElem);
            setEventElem(newElem);
            wrapListElem.insertBefore(newElem, addNewElem.parentElement);

        });
        reader.readAsDataURL(this.files[0]);

    });


    sliderElem[0].classList.add('active');

};


